/*######-------- ||| ORDRE FLEXBOX ||| --------######*/
body .conteneur .conteneur-flex{
    #zone-1.page-association{order:1;}
    #zone-2.page-association{order:2;}
    #zone-3.page-association{order:3;}
    #zone-4.page-association{}
    #zone-5.page-association{}
    @include breakpoint ($desktop){}
}

/*######-------- ||| BORDURES ||| --------######*/
body .conteneur .conteneur-flex,
body .conteneur{
    @include breakpoint(($desktop,$tablet)){
    }

}

/*######-------- ||| LAYOUT SPECIFIQUE ||| --------######*/
#zone-illustration.page-association{
    min-height: 35vw;
    max-height: 90vw;
    overflow: hidden;
    
    img{
        
            transform: translate(0%,-35%);
        }
    @include breakpoint(($desktop,$desktopwide,$mobilelandscape,$tablet,$tabletlandscape)){
        min-height: 25vw;
        max-height: 30vw;
        img{
            transform: translate(0%,-35%);
        }
    }
}
#zone-1.page-association{
    padding: span(0.2);
}

/*######-------- ||| STYLE ||| --------######*/

/*¤¤¤ STYLES COMMUNS ¤¤¤*/
h1.titre_overlay{
    @include titre_overlay();
}
#zone-1.page-association,
#zone-2.page-association,
#zone-3.page-association,
#zone-4.page-association,
#zone-5.page-association
{
    p,li,ul{
        @include font-size(m);//utilisation de typey
    }
    h2{
        color: $orange-fonce;
        @include font-size(xl);
    }
    h3{
        color: $orange-fonce;
        @include font-size(l);
    }
    h4{
        color: $orange-fonce;
        @include font-size(m);
    }
    strong{
        color: $orange-fonce;
    }
    a{
        color: $orange-fonce;
        text-decoration:none;
        &:hover{
                text-decoration: underline;
                 transition: all 0.5s ease;
        }
    }
}

/*------------------------------------
        ¤¤¤ STYLES PROPRES ¤¤¤
______________________________*/

#zone-1.page-association{
        .entrefilet{
           
            display: block;
            color: $orange-fonce;
            @include font-size(l);//utilisation de typey
            text-transform: uppercase;
            font-weight: bold;
            quotes: "\201C""\201D""\2018""\2019";
            @include breakpoint(($desktop,$desktopwide,$mobilelandscape,$tablet,$tabletlandscape)){
            padding: span(0.2);
            transform: translate(1%,2%);
            float: right;
                margin: span(0.1);
                min-width: span(3);
                max-width: span(5);
            }
            &::before{
                 /*@include backgroundImage('bg-merci.gif');*/
                 content: open-quote;
                 @include font-size(l);//utilisation de typey
            }
        }
        aside{
            img[src*="deco"]{
            @include breakpoint(($desktop,$desktopwide,$mobilelandscape,$tablet,$tabletlandscape)){
                float: right;
                margin: span(0.01);
            }
            }
            &.video{
                @include backgroundImage(('zebra-gros-clair.gif'),transparent,top left,repeat);
                @include arrondis();
                @include breakpoint(($desktop,$desktopwide,$mobilelandscape,$tablet,$tabletlandscape)){
                    padding: span(0.01);
                    margin: 0 span(2);
                }
            }
        }
        .lien-origine{
            font-weight: bold;
            position: relative;
            &::before{
                content: " >> ";
                
            }
            a{
                
                &:hover{
                color: $blanc-casse;
                background: $orange-fonce;
                text-decoration: none;
                }
            }
        }
